        }

        .merchantList {
            display: flex;
            margin-top: 7px;
            overflow-y: scroll;
            height: 135px;
            .merchantItem {
                height: 130px;
                margin-right: 12px;
                border-radius:0 0 6px 6px;
                /*box-shadow: 0 0 4px 0 rgba(194,194,194,0.50);*/
                .merchantImg {
                    width: 190px;
                    height: 130px;
                    border-radius: 6px;
                    overflow: hidden;
                    img {
